







import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class SeatChip extends Vue {
  @Prop() no!: number;
  @Prop() name!: string;
  @Prop() wolf!: boolean;

  get getColor() {
    return this.wolf ? "pink" : "primary";
  }

  get getTextColor() {
    return this.wolf ? "white" : "primary";
  }

  get outlined() {
    return !this.wolf;
  }
}
